import React, { useState } from "react";
import { useTranslation } from "react-i18next";
import {
  Form,
  FormGroup,
  Input,
  Label,
  Col,
  Button,
  Card,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  Container,
} from "reactstrap";
import { FaExchangeAlt, FaKey } from "react-icons/fa";

import "./ChangePassword.css";

import AsteriskIcon from "components/common/AsteriskIcon/AsteriskIcon";

export default function ChangePassword() {
  const { t } = useTranslation();

  const [oldPasswordInput, setOldPasswordInput] = useState("");
  const [passwordInput, setPasswordInput] = useState("");
  const [password2Input, setPassword2Input] = useState("");

  const handleOldPasswordInputChange = function (event) {
    setOldPasswordInput(event.target.value);
  };

  const handlePasswordInputChange = function (event) {
    setPasswordInput(event.target.value);
  };

  const handlePassword2InputChange = function (event) {
    setPassword2Input(event.target.value);
  };

  const handleChangeButtonOnClick = function (event) {
    event.preventDefault();
  };

  return (
    <>
      <h1>{t("CHANGE_PASSWORD").toUpperCase()}</h1>
      <Container className="mt-5">
        <Form>
          <FormGroup row className="text-left">
            <Label for="oldPassword" lg={4}>
              {t("OLDPASSWORD")} <AsteriskIcon />
            </Label>
            <Col lg={8}>
              <InputGroup>
                <InputGroupAddon addonType="prepend">
                  <InputGroupText>
                    <FaKey />
                  </InputGroupText>
                </InputGroupAddon>
                <Input
                  type="password"
                  id="oldpassword"
                  placeholder="Enter your old-password..."
                  value={oldPasswordInput}
                  onChange={handlePassword2InputChange}
                ></Input>
              </InputGroup>
            </Col>
          </FormGroup>

          <FormGroup row className="text-left">
            <Label for="password" lg={4}>
              {t("PASSWORD")} <AsteriskIcon />
            </Label>
            <Col lg={8}>
              <InputGroup>
                <InputGroupAddon addonType="prepend">
                  <InputGroupText>
                    <FaKey />
                  </InputGroupText>
                </InputGroupAddon>
                <Input
                  type="password"
                  id="password"
                  placeholder="Enter your password..."
                  value={passwordInput}
                  onChange={handlePasswordInputChange}
                ></Input>
              </InputGroup>
            </Col>
          </FormGroup>

          <FormGroup row className="text-left">
            <Label for="password2" lg={4}>
              {t("PASSWORD_AGAINST")} <AsteriskIcon />
            </Label>
            <Col lg={8}>
              <InputGroup>
                <InputGroupAddon addonType="prepend">
                  <InputGroupText>
                    <FaKey />
                  </InputGroupText>
                </InputGroupAddon>
                <Input
                  type="password"
                  id="password2"
                  placeholder="Re-enter your password..."
                  value={password2Input}
                  onChange={handlePassword2InputChange}
                ></Input>
              </InputGroup>
            </Col>
          </FormGroup>

          <Button onClick={handleChangeButtonOnClick}>
            <FaExchangeAlt /> {t("CHANGE")}
          </Button>
        </Form>
      </Container>
    </>
  );
}
